<template lang="html">
  <div class="sp">
    <router-link :to="{ name: 'articles', params: {id:list.id} }" class="list clearFix" :class="{'active':index<start}" v-for="(list,index) in lists" :key="list.id" ref="item">
      <div class="content">
        <h3 class="title">
          {{ list.title }}
        </h3>
        <div class="category">分类: {{ list.category.name }}</div>
        <div class="brief">
          {{ list.summary }}
        </div>
      </div>
    </router-link>
    <div ref='bottom'>botoom</div>
  </div>
</template>

<script>
import { mapGetters,mapActions } from 'vuex'
import scroll from '@/utils/scroll.js'
export default  {
  data () {
    return{
      start: 5
    }
  },
  computed: mapGetters({
    lists: 'comSpItem'
  }),
  methods: {
    ...mapActions(['getSpItem']),
    addItem() {
      this.start += 1;
      
    }
  },
  created() {
     if(!this.list){
      this.getSpItem();
    }
  },
  mounted () {
    function throttle(method) {
      let timer;
      return function() {
        let context = this
        let args = arguments
        if(timer){
          clearTimeout(timer);
        }
        timer = setTimeout(function(){
            method.apply(context,args);
        },200);
      }
      
    }
    function getOffset(el) {
      let top = 0,left = 0
      while (el) {
          left += el.offsetLeft;
          top += el.offsetTop;
          el = el.offsetParent
      }
      return {top,left}
    }
    this.s = scroll(document.body)
    this.s.add(throttle(this.addItem))
    this.s.on()
    window.addEventListener('scroll',()=>{
      this.bottom = this.$refs.bottom.offsetTop
      if(this.s && this.bottom && this.bottom < window.innerHeight + window.pageYOffset){
        this.s.off()
        this.s = null;
      }
    })
  },
}
</script>


<style lang="scss" scoped>
a{
  color:#fff;
  text-decoration: none;
}
@mixin to-drak {
  animation: all .2s ease-in;
  &:hover{
    opacity: 0.6;
  }
}
.sp{
  .list{
    background: #1fb5ad;
    display: block;
    padding: 16px;
    margin-bottom: 20px;
    transition: all .8s ease-in;
    transform-style: preserve-3d;
    transform-origin: center bottom;
    transform: rotateX(-50deg) translateZ(60px);
    opacity: 0;
    &.active{
      transform: rotateX(0deg) translateZ(0px);
      opacity: 1;
    }
    .content{
      @include to-drak;
      
      .category{
        color:#fafafa;
        font-size: 14px;
      }
      .brief{
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 16px;
      }
    }
  }
}
</style>



