export default function(el){
    let type = ''
    let cb = null
    function add(fn){
        cb = fn
    }
    const event = {
        mousewheel:{
            ev(e){
                if (e.wheelDelta < 0) {
                    cb()
                }
            },
            on(){
                console.log(this)
                el.addEventListener('mousewheel', this.ev)
            },
            off(){
                el.removeEventListener('mousewheel', this.ev)
            }
        },
        DOMMouseScroll:{
            ev(e){
                if (e.detail > 0) {
                    cb()
                }
            },
            on(){
                el.addEventListener('DOMMouseScroll', this.ev)
            },
            off(){
                el.removeEventListener('DOMMouseScroll', this.ev)
            }
        }
    }
    var isPhone = window.navigator.userAgent.indexOf('Mobile') > -1
    return check()
    function check(){
        if(isPhone){
            let ps = new phoneScroll(el)
            return {
                off: ps.off,
                on: ps.on,
                add: add,
            }
        }else{
            for(let key in event){
                if('on'+ key in el){
                    event[key].on()
                    type = key
                    break;
                }
            }
            return {
                add: add,
                on: event[type].on,
                off: event[type].off
            } 
        }
        
    }
    function phoneScroll(el){
        var startY,endY;
        let distance = 5;
        this.on = function(){
            console.log('in')
            el.addEventListener('touchstart', handleEvent)
            el.addEventListener('touchmove', handleEvent)
            el.addEventListener('touchend', handleEvent)
        }
        let handleEvent = function (event) {
            switch (event.type) {
                case 'touchstart':
                    doAction(event, 'START')
                    break;
                case 'touchmove':
                    doAction(event, 'MOVE')
                    break;
                case 'touchend':
                    doAction(event, 'END')
                    break;
            }
        }
       
        function doAction(e,type){
            const target = e.touches[0]
            switch(type){
                case 'START':
                    startY = endY = target.pageY
                    break
                case 'MOVE':
                    endY = target.pageY
                    break
                case 'END':
                    if (Math.abs(startY - endY) > distance && endY - startY < 0){
                        cb()
                    }
                    break
            }
        }
        this.off = function () {
            el.removeEventListener('touchstart', handleEvent)
            el.removeEventListener('touchmove', handleEvent)
            el.removeEventListener('touchend', handleEvent)
        }
    }
}



